import * as React from "react";
import { Tappable } from "../Tappable/Tappable";
import { getClassName } from "../../helpers/getClassName";
import { classNames } from "../../lib/classNames";
import { HasRootRef } from "../../types";
import { usePlatform } from "../../hooks/usePlatform";
import { Text } from "../Typography/Text/Text";
import "./SliderSwitch.css";

export interface ButtonProps
  extends React.ButtonHTMLAttributes<HTMLElement>,
    HasRootRef<HTMLElement> {
  active?: boolean;
  hovered?: boolean;
}

/**
 * @deprecated Этот компонент устарел и будет удален в 5.0.0.
 */
export const SliderSwitchButton = ({
  active,
  hovered,
  children,
  getRootRef,
  ...restProps
}: ButtonProps) => {
  const platform = usePlatform();
  const [focus, setFocus] = React.useState(false);

  const toggleFocus = () => {
    setFocus(!focus);
  };

  return (
    <Tappable
      {...restProps}
      vkuiClass={classNames(
        getClassName("SliderSwitch__button", platform),
        active && "SliderSwitch__button--active",
        !active && hovered && "SliderSwitch__button--hover",
        active && hovered && "SliderSwitch__button--activeHover",
        focus && !hovered && "SliderSwitch__button--focus"
      )}
      Component="button"
      type="button"
      getRootRef={getRootRef}
      aria-pressed={active}
      onFocus={toggleFocus}
      onBlur={toggleFocus}
      tabIndex={0}
      hasActive={false}
      hoverMode="opacity"
    >
      <Text weight="2">{children}</Text>
    </Tappable>
  );
};
